<template>
  <div>
    <section class="hero-banner-xs hero-banner--xs">
      <div class="container">
        <div class="hero-banner--sm__content">
          <h1>B-WEATHER</h1>
        </div>
      </div>
    </section>
    <marquee-text
      :repeat="text_repeat"
      :duration="text_duration"
      :paused="isPaused"
      class="card"
      @mouseenter="isPaused = !isPaused"
      @mouseleave="isPaused = false"
    >
      <span
        v-for="station in stations"
        :key="station[0].id"
        style="font-size: 1.35rem"
      >
        <span style="color: #1b396b; font-weight: 500">
          {{ station[0].station_name }}
        </span>
        <span
          v-for="card in [station[1]]"
          :key="card.id"
          class="flex mt-2"
          style="min-height: 12vh"
        >
          <span
            v-for="(marquee, index) in marquee_f(
              card.table.array_table_label,
              card.table.weather_data,
              card.table.array_act_symbol,
              [card.table.siaga]
            )"
            :key="index"
          >
            {{ marquee.sensor }} : {{ marquee.data }} {{ marquee.symbol }}
          </span>
        </span>
        <img :src="logo" style="width: 20px" class="mx-3 my-1" />
      </span>
    </marquee-text>
    <section data-aos="zoom-in" data-aos-delay="200">
      <div>
        <v-map
          :zoom="5"
          :center="initialLocation"
          style="height: 66vh; width: auto"
        >
          <v-icondefault></v-icondefault>
          <v-tilelayer
            url="https://{s}.tile.openstreetmap.fr/hot/{z}/{x}/{y}.png"
          ></v-tilelayer>
          <v-marker-cluster
            :options="clusterOptions"
            @clusterclick="click()"
            @ready="ready"
          >
            <span v-for="item in stations.map((x)=>x[0])" :key="item.id">
              <v-marker
                v-if="item.station_type == 2"
                :lat-lng="latLng(item.latitude, item.longitude)"
              >
                <v-icon>
                  <img src="@/assets/img/map/awlr25.svg" />
                </v-icon>
                <v-popup :content="`${item.station_name}`"></v-popup>
              </v-marker>
              <v-marker
                v-if="item.station_type == 1"
                :lat-lng="latLng(item.latitude, item.longitude)"
              >
                <v-icon>
                  <img src="@/assets/img/map/arr25.svg" />
                </v-icon>
                <v-popup :content="`${item.station_name}`"></v-popup>
              </v-marker>
              <v-marker
                v-if="item.station_type == 3"
                :lat-lng="latLng(item.latitude, item.longitude)"
              >
                <v-icon>
                  <img src="@/assets/img/map/aws25.svg" />
                </v-icon>
                <v-popup :content="`${item.station_name}`"></v-popup>
              </v-marker>
            </span>
          </v-marker-cluster>
          <v-control-scale
            position="bottomleft"
            :imperial="false"
            :metric="true"
          ></v-control-scale>
          <v-control position="topright">
            <a
              href="https://weatherapp.blitztechnology.tech/login"
              class="btn btn-sm"
              role="button"
              style="background-color: #2c4b83; color: #fff; font-weight: bold"
              >LOGIN</a
            >
          </v-control>
        </v-map>
      </div>
    </section>
  </div>
</template>

<script>
import * as Vue2Leaflet from "vue2-leaflet";
import { latLng } from "leaflet";
import Vue2LeafletMarkercluster from "@/components/Vue2LeafletMarkercluster";
import axios from "axios";
import MarqueeText from "@/assets/MarqueeText.vue";
import logo from "@/assets/logo-bt.svg";

export default {
  components: {
    "v-map": Vue2Leaflet.LMap,
    "v-tilelayer": Vue2Leaflet.LTileLayer,
    "v-icondefault": Vue2Leaflet.LIconDefault,
    "v-marker": Vue2Leaflet.LMarker,
    "v-popup": Vue2Leaflet.LPopup,
    "v-marker-cluster": Vue2LeafletMarkercluster,
    "v-control-scale": Vue2Leaflet.LControlScale,
    "v-control": Vue2Leaflet.LControl,
    "v-icon": Vue2Leaflet.LIcon,
    MarqueeText,
  },

  data() {
    return {
      clusterOptions: {},
      initialLocation: latLng(-1.230374, 120.498047),

      stations: [],
      isPaused: false,
      text_duration: null,
      text_repeat: null,
      logo,

      balai: null,
    };
  },
  methods: {
    click: (e) => console.log("clusterclick", e),
    ready: (e) => console.log("ready", e),
    latLng: function (lat, lng) {
      return [lat, lng];
    },
    marquee_f(a, b, c, d) {
      return a.map((card, i) => {
        return {
          sensor: card,
          data: b[i],
          symbol: c[i],
          siaga: d[i],
          role: "is_guess",
        };
      });
    },
    async loadMarquee() {
      
      this.balai = this.$fixedBalai;
      await axios
        .get(`${this.$baseURL}/balai/non-auth/${this.balai}`)
        .then((r) => {
          this.detBalai = r.data[0];
        });
      this.text_duration = this.detBalai.text_duration;
      this.text_repeat = this.detBalai.text_repeat;
      await axios
        .get(`${this.$baseURL}/home-data/0`)
        .then((r) => {
          this.stations = r.data;
        });
    },
  },
  created() {
    this.loadMarquee();
  },
  mounted() {
    setTimeout(() => {
      this.$nextTick(() => {
        this.clusterOptions = { disableClusteringAtZoom: 11 };
      });
    }, 5000);
  },
};
</script>

<style>
@import "~leaflet/dist/leaflet.css";
@import "~leaflet.markercluster/dist/MarkerCluster.css";
@import "~leaflet.markercluster/dist/MarkerCluster.Default.css";
html,
body {
  height: 100%;
  margin: 0;
}
</style>
